// Layout to display any podcast badges

.podcast-badges {
    display: grid;
    grid-template-columns: repeat(auto-fill, 130px);
    justify-content: start;
    gap: 5px;

    // reduce the side border on the grid if mobile
    @include for-size(phone-only){
        justify-content: center;
    }
  }

.podcast-badge {
    display: grid;
    width: auto;
    height: 100%;
}

